/* src/Dashboard.css */
.dashboard {
  display: flex;

  font-family: Arial, sans-serif;
  background-color: #ffffff;
  /* height: 100vh; */
}

/* .active-filter-btn:active{
  background-color: red;
} */

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.viewall {
  color: var(--main-light-clr);
  font-family: Sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration-line: underline;
  text-decoration-style: solid;
  text-decoration-skip-ink: auto;
  text-decoration-thickness: auto;
  text-underline-offset: auto;
  text-underline-position: from-font;
}

.sidebar {
  width: 200px;
  background-color: var(--main-light-clr);
  color: white;
  padding: 20px;
  height: fit-content;
  border-bottom-right-radius: 5px;
}

.Admin-box {
  display: flex;
  justify-content: space-between;
}
.Admin-box img {
  width: 40px;
}

.sidebar .logo {
  font-size: 24px;
  margin-bottom: 20px;
  color: #fff;
}

.sidebar .menu {
  display: flex;
  flex-direction: column;
}

.sidebar .menu .menu-button {
  background: none;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  text-align: left;
  width: 100%;
}

.sidebar .menu .menu-button:hover {
  border-radius: 10px;
  background-color: rgba(44, 132, 232, 0.9);
}
.main-content {
  margin: 5px;
  flex: 1;
  /* padding: 20px; */
  border-radius: 7px;
  /* width: 80%; */
  background: #feffff;
  /* margin: 20px; */
  color: #000;
  border-left:1px solid var(--main-lighters-clr) ;
  border-top:1px solid var(--main-lighters-clr) ;
  border-right:1px solid var(--main-lighters-clr) ;
  border-bottom:1px solid var(--main-lighters-clr) ;
}

.main-content .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.main-content .header .user-info {
  display: flex;
  align-items: center;
}


.main-content .header .user-info .user-avatar {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  margin-right: 10px;
}


.main-content .cards {
  display: grid;
  max-width: 220px;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-top: 20px;
}

.card:hover {
  transition: 1s;
  transform: translateY(-5px);
}

.main-dash {
  color: var(--main-light-clr);
  font-family: Sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}


.main-content .cards .card {
  width: 220px;
  height: 120px;
  background-color: white;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 7px;
  border: 1px solid #b5ddfb;
  background: #feffff;
}

.profile-container{
  display: flex;
}


.main-content .cards .card h3 {
  color: #575859;
  font-family: Sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}


.total-number {
  color: #1d3557;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.cards a {
  text-decoration: none;
}


@media  (max-width: 480px) {
  .main-content {
    flex: 1 1;
    padding: 10px;
    border-radius: 7px;
    background: #feffff;
    
}
.profile-container{
  display: block;
}

}
