@media (min-width: 1px) and (max-width: 480px) {
  .property-filters {
    display: none;
  }
  .property-card-info {
    margin-left: 0px;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 10px;
  }

  .property-post-filters-box {
    width: 80%;
    display: block;
    /* margin: 5px auto; */
  }
  .property-post-filters-box {
    display: block;
  }
  

  .property-post-card-main {
    margin: 10px;
    width: auto;
    height: auto;
    /* padding: 15px; */
    border-radius: 8px;
    background: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  }
  .imageSlide img {
    /* width: auto; */
    /* height: auto; */
    min-width: 100%;
    max-width: 100%;
    transition: all 1s ease-in-out;
  }
  .property-post-card {
    display: grid;
    /* justify-content: space-between;
    position: relative; */
  }
}
