.rental-agreement-card {
  background: radial-gradient(50% 50% at 50% 50%, #f6f6ff 7.4%, #91f2e2 100%);
  box-shadow: 0px 4px 11.2px 0px rgba(55, 55, 55, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 56px;
  width: 400px;
  padding: 40px 0;
  border-radius: 10px;
  margin: 10px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.rental-agreement-icons {
  display: flex;
  gap: 20px;
}

.rental-agreement-icons img {
  width: 71px;
  height: 60px;
}

.rental-agreement-content {
  text-align: center;
}

.rental-agreement-title {
  font-size: 1.5rem;
  color: #2c2c2c;
  margin-bottom: 10px;
}

.rental-agreement-description {
  font-size: 1rem;
  color: #2c2c2c;
  margin-bottom: 20px;
}

.rental-agreement-image img {
  width: 100%;
  height: auto;
  max-width: 200px;
}

.rental-agreement-button {
  border-radius: 57px;
  background: #22796b;
  display: flex;
  width: 295px;
  padding: 10px 20px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  color: #f7f7f7;
  text-align: center;
  border: none;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.8px;
  cursor: pointer;
}

.rental-agreement-button:hover {
  opacity: 0.7;
  color: #fff;
}

/* report section */

/* ActionBar.css */

.action-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  gap: 16px;
  border-radius: 5px;
  text-decoration-line: underline;

  width: 400px;
  /* margin: 20px 10px; */
  border-radius: 7px;
  border: 0.1px solid #a4d0ed;
  background: #fff;
  box-shadow: 0px 0px 4px 2px rgba(226, 242, 255, 0.5);
}
.action-bar img {
  width: 20px;
  height: 20px;
}

.action-bar p {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
}

.action-item {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 16px;
  color: #4a4a4a;
}

.report-icon {
  font-size: 20px;
}

.whatsapp-icon {
  background-color: #25d366; /* WhatsApp Green */
  color: white;
  border-radius: 50%;
  padding: 5px;
  font-size: 18px;
}

.share-icon {
  font-size: 20px;
}

.action-text {
  color: #30307b;
  text-align: center;
  font-family: Sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration-line: underline;
  text-decoration-style: solid;
  text-decoration-skip-ink: none;
  text-decoration-thickness: auto;
  text-underline-offset: auto;
  text-underline-position: from-font;
}

.action-item:hover {
  cursor: pointer;
  opacity: 0.8;
}

