/* Add new Css -- Amit  */

.prop-content-main {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.prop-left {
  flex: 1;
}

/* Amit chaudhary css */

#property-dir {
  position: relative;
}
.main_hide {
  position: absolute;
  bottom: 80px;
  /* left: 50%; Center horizontally */
  transform: translateX(-50%); /* Centering the popup */
  z-index: 1; /* Ensure it appears above other content */
  pointer-events: none; /* Avoid interaction while hidden */
  transition: opacity 0.3s ease, transform 0.3s ease; /* Smooth transition */
  opacity: 1; /* Make visible when the show class is added */
  pointer-events: auto; /* Allow interaction */
  transform: translateX(-50%) translateY(10px); /* Move it down slightly to create smooth entrance */
}

.postDirect_detail {
  background-color: var(--main-light-clr);
  color: white; /* White text */
  padding: 12px 25px; /* Padding for spacing */
  border-radius: 10px; /* Rounded corners */
  font-family: Arial, sans-serif; /* Font style */
  font-size: 13px; /* Adjust font size */
  max-width: 300px; /* Set max-width for the message */
  text-align: start; /* Center text inside the box */
  box-shadow: 0 4px 10px rgba(12, 11, 11, 0.3); /* Add shadow for depth */
  width: 50vw;

  max-width: 300px;
  /* min-width: 40vw */
}
.i-icon-section {
  z-index: 3;
  background-color: #fff;

  border: none;
  /* padding: 3px 6px; */
  cursor: pointer;
  margin-left: 5px;
}

@media (max-width: 1024px) {
  .property-details-container {
    border-radius: 10px;
    padding: 0px;
    width: 90%;
    margin: 20px auto;
  }
  .prop-content-main {
    width: 100%;

    margin-top: 20px;
  }
}

@media (max-width: 768px) {
  .property-details-container {
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }
  .prop-content-main {
    flex-direction: column;
    align-items: center;
  }
  .property-image {
    width: 100%;
  }

  .prop-right {
    margin-left: 0;
    width: 100%;
  }

  .property-info {
    width: 100%;
    padding: 15px;
  }

  .property-title {
    font-size: 18px;
  }

  .property-price {
    font-size: 16px;
  }

  .monthly-price {
    font-size: 12px;
  }

  .property-info-tags {
    flex-direction: column;
    gap: 5px;
  }

  .property-actions {
    flex-direction: column;
    gap: 10px;
  }

  .contact-expert-btn {
    font-size: 14px;
    padding: 10px;
    text-align: center;
    width: fit-content;
  }

  .map-iframe {
    width: 100%;
    height: 250px;
  }

  .map-para,
  .map-para1 {
    font-size: 12px;
    padding: 10px;
  }

  .locations-grid {
    grid-template-columns: repeat(2, 1fr);
    padding: 10px;
  }

  .location-item {
    justify-content: flex-start;
    height: auto;
    padding: 10px;
  }

  .loan-available-card,
  .rental-agreement-card {
    width: 100%;
    margin: 10px 0;
  }

  .amenities-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  .amenity-item {
    width: 100px;
    padding: 5px;
  }

  .action-bar {
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 15px;
    width: 100%;
  }

  .action-item {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 5px;
  }

  .property-actions {
    display: flex;
    flex-direction: row;
    gap: 10px;
  }

  .property-info {
    border-radius: 12px;
    background: #fff;
    box-shadow: 0px 0px 4px 2px rgba(156, 197, 231, 0.5);
    width: 100%;
    margin-left: 0;
    padding: 20px;
    flex-shrink: 0;
    flex-grow: 0;
  }
}
@media (max-width: 480px) {
  .prop-id-name {
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 8px;
    padding: 0 5px;
    color: #fff;
    border-radius: 43px;
    background: #0078d4;
    display: flex;
    align-items: center;
  }

  .details-heading-section {
    display: flex;
    justify-content: space-between;
    border-radius: 7px;
    border: 1px solid #a4d0ed;
    background: #fff;
    box-shadow: 0px 0px 4px 2px rgba(226, 242, 255, 0.5);
    padding: 8px 15px;
  }

  .icon-overview {
    width: 22px;
    /* height: 30px; */
    margin-right: 20px;
  }

  .Property-detail-heading {
    color: #4a4a4a;

    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    margin-bottom: 10px;
    line-height: normal;
  }
  .property-details-container {
    padding: 5px;
  }
  .property-info {
    border-radius: 12px;
    border-radius: 12px;
    /* border: 1px solid #a4d0ed; */
    border-radius: 12px;
    background: #fff;
    box-shadow: 0px 0px 4px 2px rgba(156, 197, 231, 0.5);
    /* width: 400px; */
    margin-left: 0px;
    padding: 20px;
    flex-shrink: 0;
    flex-grow: 0;
  }
  .property-image {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    gap: 10px;
  }

  .prop-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .property-info-tags {
    flex-direction: row;
    gap: 5px;
  }

  .eoi-form-container {
    border: 1px solid #b9b9b9;
    color: #000;
    padding: 20px;
    width: auto;
    border-radius: 8px;
    margin-top: 30px;
  }
  .property-actions {
    flex-direction: row;
    gap: 10px;
  }
  .property-info-tags {
    display: flex;
    align-items: start;
    gap: 10px;
    margin-bottom: 10px;
  }
  .action-bar {
    flex-direction: row;
    align-items: center;

    width: 90%;
  }

  .property-info-mobile {
    border-radius: 12px;
    background: #f6f6ff;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    width: 95%;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
  }

  .overbox-icon {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  .property-details-container {
    border-radius: 10px;
    padding: 0;
    width: 90%;
    margin: 20px auto;
  }

  .p-ans {
    color: #111;
    font-size: 12px;
    font-weight: 600;
    margin-top: 5px;
    letter-spacing: 0.8px;
  }

  .property-info {
    /* display: none; */
    padding: 10px;
  }

  .property-price {
    font-size: 14px;
  }

  .monthly-price {
    font-size: 10px;
  }

  .contact-expert-btn {
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 12px;
    padding: 8px;
  }

  .map-iframe {
    height: 200px;
  }

  .amenities-grid {
    grid-template-columns: 1fr;
  }

  .amenity-item {
    width: 90px;
  }

  .loan-available-card-content h4,
  .rental-agreement-title {
    font-size: 14px;
  }

  .loan-available-card-content p,
  .rental-agreement-description {
    font-size: 10px;
  }

  .loan-available-card-primary-button,
  .rental-agreement-button {
    font-size: 12px;
    padding: 5px 10px;
  }

  .locations-grid {
    grid-template-columns: 1fr;
  }

  .location-item {
    padding: 5px;
  }

  .action-item {
    font-size: 12px;
  }
  .postDirect_detail {
    width: 60vw;
  }
  .main_hide {
    left: -20px;
  }
}

