/* General Reset */
* {

  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Sans-serif", sans-serif;
  text-decoration: none;
}

.header-free {
  background-color: var(--main-light-clr);
  color: white;
  border-radius: 20px;
  padding: 3px;
  font-size: 10px;
  margin: 5px;
  font-weight: 500;
}
.nav-computer-item {
 
  border-radius: 50%;

}

/* Navbar styles */
.navbar {
  background-color: #fff;
  padding: 12px 0; /* Decreased padding */
  position: sticky;
  top: 0;
  z-index: 3;
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.25);
}

.nav-dropdown {
  transition: 1s;
  border-radius: 5px;
  border: 1px solid var(--main-light-clr
  );
}
.dropbtn {
  display: flex;
  justify-content: space-between;
  /* margin: 0 10px 0 0 ; */
 width: 25px;
}
.btn-drop {
  width: 110px;
  cursor: pointer;
  padding: 5px 15px;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%; /* Decreased width */
  margin: 0 auto;
}

.logo h2 {
  font-size: 20px;
    color: #111;
    cursor: pointer;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 5px;
    border-radius: 5px;
    border: 2px solid #333;
}
.name-user-nav{
  color: var(--main-light-clr);
  background-color: var(--main-lighter-clr);
  padding: 5px;
  border-radius: 50%;
  font-size: 12px;
  font-weight: 600;
}
.icons-nav {
  width: 22px;
}

.logo img {
  width: 160px;
}

.nav-links {
  display: flex;
  align-items: center;
  list-style: none;
  gap: 18px; /* Decreased gap */
}

.nav-link {
  font-size: 14.4px; /* Decreased font size */
  font-weight: 500;
  text-decoration: none;
  color: #111;
  cursor: pointer;
  transition: all 0.3s ease;
}

.nav-link:hover {
  color: #ceced3;
}

.nav-link.sell-btn {
  font-weight: 600;
  padding: 9px 13.5px; /* Decreased padding */
  border: 1px solid var(--main-light-clr);
  color: var(--main-light-clr);
  border-radius: 4.5px; /* Decreased border radius */
  transition: background-color 0.3s ease;
}

.nav-link.sell-btn:hover {
  background-color: var(--main-light-clr);
  color: #fff;
}

.nav-link.buy-btn {
  padding: 9px 13.5px; /* Decreased padding */
  background-color: var(--main-light-clr);
  color: #fff;
  border-radius: 4.5px; /* Decreased border radius */
  transition: background-color 0.3s ease;
}

.nav-link.buy-btn:hover {
  background-color: var(--main-light-hover-clr);
}

.userimg-logo-box {
  display: flex;
  align-items: center;
  cursor: pointer;
  gap: 9px; /* Decreased gap */
}

.userimg-logo {
  width: 36px; /* Decreased width */
  height: 36px; /* Decreased height */
  object-fit: cover;
  border-radius: 50%;
  border: 1.8px solid #3361e0; /* Decreased border width */
}

.userimg-logo-box p {
  font-size: 14.4px; /* Decreased font size */
  color: #111;
  font-weight: 600;
}

.userimg-logo-box:hover p {
  color: #3361e0;
}

.UserForm-Container > .user-form {
  background-color: #fff;
  padding: 18px; /* Decreased padding */
  border-radius: 7.2px; /* Decreased border radius */
  box-shadow: 0 3.6px 9px rgba(0, 0, 0, 0.1); /* Decreased box-shadow */
  max-width: 360px; /* Decreased width */
  width: 90%;
}

.UserForm-Container > .user-form .close-btn {
  position: absolute;
  top: 9px; /* Decreased position */
  right: 9px; /* Decreased position */
  font-size: 18px; /* Decreased font size */
  cursor: pointer;
}

.navbar .nav-links a {
  text-decoration: none;
  color: inherit;
}

.navbar .nav-links a:hover {
  color: #3361e0;
}

/* nav drop Donw Css */

.dropdown-content {
  background-color: var(--main-lighter-clr);
  margin-top: 10px;
  position: absolute;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  border: 1px solid #b5d6f1;
  box-shadow: 0px 0px 10px 0px #eaf1f6;
}
.Dropdown-section {
  padding: 5px;
  width: 110px;
}
.Dropdown-section:hover {
  background-color: var(--main-light-clr);
  padding: 5px;
  width: 110px;
  color: #fff;
  /* border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px; */
}
@media (min-width: 480px) and (max-width: 2100px){

  .hide-for-pc{
    display: none;
  }

}

