@media (max-width: 768px){
  .navbar-container {
    display: flex
;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    margin: 0 auto;
}
.nav-links {
  display: flex
;
  align-items: center;
  list-style: none;
  gap: 10px;
}
}

@media (max-width: 480px) {

  .btn-drop {
    width: 90px;
    cursor: pointer;
    padding: 0px 15px;
}
 
  .dropdown-content {
    font-size: 12px;
    background-color: var(--main-lighter-clr);
    margin-top: 10px;
    position: absolute;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    border: 1px solid #b5d6f1;
    box-shadow: 0px 0px 10px 0px #eaf1f6;
  }
  .header-free {
    display: none;
  }
  .navbar {
    background-color: #F2F9FF;
    padding: 0; 
    position: sticky;
    top: 0;
    z-index: 3;
    /* border-bottom: 2px solid #e9e6e6; */
  }

  .nav-link.sell-btn {
    display: none;
  }
  .logo h2 {
    border: none;
    font-size: 16px; /* Decreased font size */
    color: var(--main-light-clr);
    cursor: pointer;
    display: flex;
    align-items: center;
    text-decoration: none;
  }
  .navbar-container {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 0 auto;
  }
}
@media (max-width: 320px){
  .logo h2{
    font-size: 14px;
  }
  .btn-drop {
    width: 90px;
    cursor: pointer;
    padding: 5px 10px;
}

}
