/* OTP Component Styles */
.otp-wrapper-unique {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 900px;
  height: 500px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
  background-color: var(--main-lighter-clr, #f9f9f9);
  transition: all 0.3s ease-in-out;
  padding: 20px;
}
.resend-otp-btn {
  font-size: 10px;
  color: var(--main-light-clr);
  font-weight: 600;
  background-color: #fff;
  padding: 2px;
  border: none;
  margin-right: 5px;
  cursor: pointer;
}
.resend-otp-btn:hover {
  border: none;
  background-color: #fff;
}
.mail-spam {
  color: #d80d0d;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.otp-section {
  padding: 10px;
  border: 3px solid var(--main-lighter-clr);
  /* border: none; */
}

.otp-form-main {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5%;
  padding: 20px;
}

.otp-image-section-unique {
  width: 50%;
  height: 100%;
}

.otp-image-section-unique img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px 0 0 10px;
}

.otp-form-content-unique {
  padding: 40px;
  width: 50%;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  border-left: 1px solid #f0f0f0;
  border-radius: 0 10px 10px 0;
}

.otp-form-content-unique h2 {
  color: var(--primary-color, #0078d4);
  font-family: Inter, sans-serif;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.8px;
  margin-bottom: 12px;
}

.otp-timer {
  font-size: 16px;
  font-weight: bold;
  color: #ff4d4f; /* Highlighted red timer for urgency */
  margin-bottom: 20px;
}

.otp-form-content-unique form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.otp-form-content-unique input[type="text"] {
  border-radius: 7px;
  border: 1px solid #daeaf9;
  display: flex;
  height: 40px;
  padding: 0px 10px;
  align-items: center;
  gap: 6px;
  align-self: stretch;
}

.otp-form-content-unique input[type="text"]:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.Continue-otp {
  width: 100%;
  padding: 12px;
  background-color: #007bff;
  color: white;
  font-size: 18px;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.otp-form-content-unique button:hover {
  background-color: #0056b3;
  transform: scale(1.02);
}

.resend-otp-button {
  margin-top: 10px;
  padding: 10px;
  background-color: #ff4d4f;
  color: white;
  font-size: 14px;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.resend-otp-button:hover {
  background-color: #cc0000;
  transform: scale(1.02);
}

/* Additional media queries for responsiveness */
@media (max-width: 768px) {
  .otp-wrapper-unique {
    flex-direction: column;
    height: auto;
  }

  .otp-image-section-unique {
    width: 100%;
    height: 200px;
  }

  .otp-form-content-unique {
    width: 100%;
    border-left: none;
    border-top: 1px solid #f0f0f0;
    border-radius: 0 0 10px 10px;
  }
}
