.userDataSection {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.profile-main-box {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 10px;
  border-radius: 10px;
}

.profile-name-ans {
  border-radius: 7px;
  border: 1px solid #daeaf9;
  background: #fff;
  padding: 10px;
}

.userDataSection p {
  margin-top: 10px;
}

.ans {
  display: inline-block;
  margin-bottom: 24px;
}

.profile-regis-ans {
  display: inline-block;
  border-radius: 20px;
  background: var(--main-light-clr);
  color: #fff;
  font-family: Sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 1.5px;
  padding: 5px 10px;
}

.whatapp-notify {
  display: flex;
  align-items: center;
}

.icons {
  width: 28px;
  margin-right: 10px;
}

.Shortlisted-prop {
  font-size: 16px;
  color: #111;
  text-align: center;
}

@media (max-width: 480px) {
  .userDataSection p {
    margin-top: 12px;
    font-size: 12px;
  }

  .profile-regis-ans {
    display: inline-block;
    font-size: 14px;
    border-radius: 20px;
    background: var(--main-light-clr);
    color: #fff;
    font-family: Sans-serif;

    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 1.5px;
    padding: 5px 10px;
  }

  .whatapp-notify p {
    font-size: 14px;
  }
  .whatapp-notify img {
    width: 20px;
  }
}


@media (max-width: 320px){
  .userDataSection p {
    margin-top: 12px;
    font-size: 10px;
}
}